/**
 * Component:	Variables
 * Description:	Define all variables
*/
/* Disable base
 -------------------------- */
/* Input
------------------*/
*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none;
}
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  overflow: auto;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td, iframe {
  margin: 0;
  padding: 0;
}
img, article, aside, details, figcaption, figure, footer, header, menu, nav, section, summary, time, mark, audio, video {
  display: block;
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
}
fieldset, img {
  border: 0;
}
address, caption, cite, dfn, em, th, var, i, em {
  font-style: normal;
  font-weight: normal;
}
ol, ul {
  list-style: none;
}
a {
  color: #4078c0;
  text-decoration: none;
}
a:hover {
    text-decoration: none;
}
code {
  background-color: #f9fafc;
  padding: 0 4px;
  border: 1px solid #eaeefb;
  border-radius: 4px;
}
a, label, button, input, select {
  -webkit-tap-highlight-color: transparent;
}
button, input, select, textarea {
  outline: none;
  font: 100% tahoma, \5b8b\4f53, arial;
  line-height: inherit;
  color: inherit;
  vertical-align: baseline;
  border-radius: 0;
  background-color: transparent;
}
textarea {
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  overflow: auto;
  resize: none;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
}
button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none;
}
input[type=checkbox], input[type=radio] {
  vertical-align: middle;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  margin: 0;
}
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px white inset;
}
@font-face {
  font-family: "YDUI-INLAY";
  src: url(data:application/x-font-ttf;base64,) format("truetype");
}
@font-face {
  font-family: "element";
  src: url("//at.alicdn.com/t/font_1473762766_5418587.eot");
  /* IE9*/
  src: url("//at.alicdn.com/t/font_1473762766_5418587.eot?#iefix") format("embedded-opentype"), url("data:application/x-font-woff;charset=utf-8;base64,") format("woff"), url("//at.alicdn.com/t/font_1473762766_5418587.ttf") format("truetype"), url("//at.alicdn.com/t/font_1473762766_5418587.svg#elementdoc") format("svg");
  /* iOS 4.1- */
  src: url("//at.alicdn.com/t/font_1473762766_5418587.eot") \0;
  /* ie8 fix */
}
.element {
  font-family: "element" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.icon-github:before {
  content: "\e603";
}
.icon-weixin:before {
  content: "\e601";
}
.g-fix-ios-overflow-scrolling-bug {
  -webkit-overflow-scrolling: auto !important;
}
.app {
  height: 100%;
  min-height: 100%;
}
.app .app-body {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
}
@media screen and (max-width: 1059px) {
.app .app-body {
      padding-top: 80px;
}
}
@media screen and (max-width: 999px) {
.app .app-body {
      padding-top: 80px;
      padding-bottom: 15px;
}
.app.app-home-view .app-body {
      padding-top: 80px;
      padding-bottom: 0;
}
}
.hljs {
  line-height: 1.8;
  font-family: Menlo, Monaco, Consolas, Courier, monospace;
  font-size: 12px;
  padding: 18px 24px;
  background-color: #f9fafc;
  border: solid 1px #eaeefb;
  margin-bottom: 25px;
  border-radius: 4px;
  -webkit-font-smoothing: auto;
}
.app-body {
  margin-top: -80px;
  padding: 80px 0 120px;
  box-sizing: border-box;
  min-height: 100%;
}
.container,
.page-container {
  width: 1140px;
  padding: 0 30px;
  margin: 0 auto;
}
.page-container {
  padding-top: 55px;
}
.page-container h2 {
    font-size: 28px;
    color: #1f2d3d;
    margin: 0;
}
.page-container h3 {
    font-size: 22px;
}
.page-container h2, .page-container h3, .page-container h4, .page-container h5 {
    font-weight: normal;
    color: #1f2f3d;
}
.page-container h2:hover a, .page-container h3:hover a, .page-container h4:hover a, .page-container h5:hover a {
      opacity: .4;
}
.page-container h2 a, .page-container h3 a, .page-container h4 a, .page-container h5 a {
      float: left;
      margin-left: -20px;
      opacity: 0;
      cursor: pointer;
}
.page-container h2 a:hover, .page-container h3 a:hover, .page-container h4 a:hover, .page-container h5 a:hover {
        opacity: .4;
}
.page-container p {
    font-size: 14px;
    color: #5e6d82;
    line-height: 1.5em;
}
.page-container .tip {
    padding: 8px 16px;
    background-color: #ECF8FF;
    border-radius: 4px;
    border-left: #50bfff 5px solid;
}
.page-container .tip code {
      background-color: rgba(255, 255, 255, 0.7);
      color: #445368;
}
.demo {
  margin: 20px 0;
}
@media (max-width: 1140px) {
.container,
  .page-container {
    width: 100%;
}
}
@media (max-width: 768px) {
.container,
  .page-container {
    padding: 0 20px;
}
}



